
import { defineComponent } from "vue";

export default defineComponent({
  name: "HelloWorld",
  props: {
    geolocation: {
      type: String,
      require: false,
    },
    location: {
      type: String,
      require: true,
    },
    img: {
      type: String,
      require: true,
    },
    weather: {
      type: String,
      require: true,
    },
    temp: {
      type: String,
      require: true,
    },
  },
  setup(props, context) {
    const isLoaded = () => {
      context.emit("isLoaded");
    };
    const modalClose = () => {
      context.emit("modalClose");
    };
    return {
      isLoaded,
      modalClose,
    };
  },
});
